import React from "react";
import "react-awesome-slider/dist/styles.css";
import AwesomeSlider from "react-awesome-slider";
import "./Slider.css";
import withAutoplay from "react-awesome-slider/dist/autoplay";
import imageBack from "../images/OIP.jpg";
import imgOne from "../images/React js_Quize/Quize_one.png";
import imgTwo from "../images/React js_Quize/Quize_two.png";
import imgThree from "../images/React js_Quize/Quize_three.png";

const deliveryData = [
  {
    image: imgOne,
  },
  {
    image: imgTwo,
  },
  {
    image: imgThree,
  },
];
const AutoplaySlider = withAutoplay(AwesomeSlider);

const SliderThree = () => {
  return (
    <>
      <div className=" w-[50vw] h-[40vh] slidebox">
        <div
          id="background_slider"
          className="w-full mt-2 md:my-4 m-auto md:mt-20"
        >
          <h1
            className="uppercase text-green-400	font-semibold text-2xl text-center my-4"
            id="categoriesTitle"
          >
            React Quize App
          </h1>

          <div className="flex justify-center items-center  ">
            <a href="https://pasupathirock.github.io/Quize_React/">
              {" "}
              <button className="bg-black text-slate-100 border-2 font-semibold  p-1 rounded-lg cursor-pointer hover:duration-400 hover:-translate-y-1 hover:transition hover:ease-in-out hover:bg-slate-100 hover:text-black ">
                {" "}
                Demo
              </button>
            </a>
          </div>

          <div className="w-full relative mt-2">
            <img
              src={imageBack}
              className="w-screen "
              alt="delivery background"
            />

            <AutoplaySlider
              className="deliverySlider awssld__content"
              mobileTouch={true}
              play={true}
              cancelOnInteraction={false} 
              interval={5000}
            >
              {deliveryData.map((slide, index) => {
                return (
                  <div key={index}>
                    <img
                      src={slide.image}
                      className="w-[100%]  m-auto md:w-[100%]"
                      alt="slider"
                    />
                  </div>
                );
              })}
            </AutoplaySlider>
          </div>
        </div>
      </div>
    </>
  );
};

export default SliderThree;
